import React from 'react';
import { useNavigate } from 'react-router-dom';
import './MainMenuCalculator.css';

function MainMenu() {
  const navigate = useNavigate();

  const handleSelection = (item) => {
    navigate('/materials', { state: { selectedItem: item } });
  };

  const items = [
    { name: '', image: `${process.env.PUBLIC_URL}/images/bb.png` },
    { name: '', image: `${process.env.PUBLIC_URL}/images/tnt.png` },
    { name: ' 4', image: `${process.env.PUBLIC_URL}/images/c4.png` },
    { name: '', image: `${process.env.PUBLIC_URL}/images/geks.png` },
    { name: ' ', image: `${process.env.PUBLIC_URL}/images/simplerocket.png` },
    { name: '', image: `${process.env.PUBLIC_URL}/images/rocket.png` },
    { name: '', image: `${process.env.PUBLIC_URL}/images/granade.png` },
    { name: '  ', image: `${process.env.PUBLIC_URL}/images/granadewith.png` },
    { name: ' ', image: `${process.env.PUBLIC_URL}/images/rocketrszo.png` }
  ];

  return (
    <div className="main-menu">
      <h1>  </h1>
      <div className="items-container">
        {items.map((item) => (
          <div key={item.name} className="item" onClick={() => handleSelection(item.name)}>
            <img src={item.image} alt={item.name} />
            <p>{item.name}</p>
          </div>
        ))}
      </div>
    </div>
  );
}

export default MainMenu;


