// src/components/Projects.js
import React from 'react';
import { useNavigate } from 'react-router-dom';
import styles from './Projects.module.css';

const Projects = () => {
  const navigate = useNavigate();

  const handleRaidCalculator = () => {
    navigate('/main-menu');
  };

  const handleExplosionCalculator = () => {
    navigate('/main-menu-explosions');
  };

  const handleTalentAwakening = () => {
    navigate('/TalentMenu');
  };

  return (
    <div className={styles.about}>
      <h1> </h1>
      <p>   ,     .</p>
      <h2></h2>
      <div className="button-container">
        <button onClick={handleRaidCalculator}> </button>
        <button onClick={handleExplosionCalculator}> </button>
      </div>
      <h2></h2>
      <div className="button-container">
        <button onClick={handleTalentAwakening}> </button>
        <button> </button>
      </div>
    </div>
  );
};

export default Projects;




