import React from 'react';
import { Link } from 'react-router-dom';
import './MainMenu.css';

function MainMenu() {
  return (
    <div className="MainMenu">
      <h2> </h2>
      <div className="talentsmenu">
        <div className="talentmenu">
          <img src={`${process.env.PUBLIC_URL}/images/instinkt.jpg`} alt="" />
          <h2></h2>
          <Link to="/instinct">
            <button></button>
          </Link>
        </div>
        <div className="talentmenu">
          <img src={`${process.env.PUBLIC_URL}/images/inellect.jpg`} alt="" />
          <h2></h2>
          <Link to="/intellect">
            <button></button>
          </Link>
        </div>
        <div className="talentmenu">
          <img src={`${process.env.PUBLIC_URL}/images/Boy.jpg`} alt="" />
          <h2></h2>
          <Link to="/fight">
            <button></button>
          </Link>
        </div>
        <div className="talentmenu">
          <img src={`${process.env.PUBLIC_URL}/images/PriseList.jpg`} alt=" " />
          <h2> </h2>
          <Link to="/pricelist">
            <button></button>
          </Link>
        </div>
      </div>
    </div>
  );
}

export default MainMenu;





